.background {
  width: 100%;
  height: 100%;
  z-index: 0;
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(0.2rem);
}

.modal {
  font-size: 2rem;
  z-index: 2;
  position: absolute;
  height: 50rem;
  left: 50%;
  transform: translateX(-50%);
  padding: 1rem;
  padding-bottom: 6rem;
  top: 8rem;
  background: #00a5ff;
  border: 0.1rem solid #000;
  border-radius: 1rem;
  width: 30rem;

  h1 {
    height: 9rem;
    vertical-align: middle;
    font-size: 3.5rem;
    margin-top: 2rem;
    color: white;
    font-weight: 700;
    word-break: keep-all;
    text-align: center;
  }
  .close {
    position: absolute;
    bottom: 0;
  }

  h3 {
    margin: 10px 0;
  }
  .group {
    position: relative;
    height: 9rem;
    background-color: white;
    border-radius: 1rem;
    padding: 1rem;
    margin-bottom: 1rem;
    vertical-align: middle;
    p {
      display: block;
      font-size: 2.5rem;
      font-weight: 600;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
    }
    p:nth-of-type(1) {
      left: 1rem;
    }
    p:nth-of-type(2) {
      left: 14rem;
    }
    input {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      margin-top: 0.2rem;
      left: 8rem;
      width: 5rem;
      height: 3rem;
      font-size: 3rem;
      font-weight: 600;
      outline: none;
      text-align: center;
      border: none;
      border-bottom: 0.1rem solid #000;
      background: none;
    }
    .buttonGroup {
      position: absolute;
      width: 12rem;
      height: auto;
      top: 50%;
      transform: translateY(-50%);
      right: 0rem;
      button {
        margin: 0.3rem;
        float: left;
        font-size: 2rem;
        width: 5rem;
        height: 3rem;
        cursor: pointer;
        border: 0.1rem solid #000;
        border-radius: 0.5rem;
        background: none;
      }
      button:active {
        background: #00a5ff;
      }
    }
  }
  .selectButton {
    position: absolute;
    width: 21rem;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0.5rem;
    button {
      margin: 1rem;
      float: left;
      font-size: 2.5rem;
      width: 8rem;
      height: 4rem;
      cursor: pointer;
      border: 0.1rem solid #000;
      border-radius: 0.5rem;
      background: white;
    }
    button:active {
      background: #00a5ff;
    }
  }
}
