.form {
  width: 94%;
  height: 10rem;
  min-height: 6rem;
  display: flex;
  margin: 0 auto;
  margin-top: 1rem;
  border: 0.1rem solid #000;
  border-radius: 1rem;
  position: relative;
  background: #00a5ff;
  color: white;
  .detail {
    font-size: 2rem;
    font-weight: 600;
    position: absolute;
    right: 2.5rem;
    top: 0.5rem;
    p {
      display: block;
      text-align: right;
      margin: 0;
      margin-top: 1rem;
    }
  }
  span {
    font-size: 2.2rem;
    font-weight: 600;
    display: block;
    width: 16rem;
    position: absolute;
    top: 3.5rem;
    left: 1rem;
    transform: translateY(-50%);
    word-break: keep-all;
    text-align: left;
  }
  .start {
    position: absolute;
    top: 50%;
    right: 2rem;
    transform: translateY(-50%);
    width: 14rem;
    height: 3.5rem;
    z-index: 0;
    font-size: 1.5rem;
    font-weight: 600;
    background: white;
    border: 0.1rem solid #000;
    border-radius: 0.5rem;
  }
}
