.exBtnGroup {
  position: absolute;
  width: 28rem;
  left: 50%;
  top: 2rem;
  transform: translateX(-50%);
  .exBtn {
    width: 9rem;
    height: 3.5rem;
    margin-right: 0.5rem;
    margin-bottom: 0.5rem;
    color: black;
    background: white;
    border: 0.1rem solid #000;
    cursor: pointer;
    font-size: 2rem;
    font-weight: 500;
    border-radius: 0.5rem;
  }
  .exBtn:nth-of-type(3) {
    margin-right: 0;
  }
  .exBtn:nth-of-type(6) {
    margin-right: 0;
  }

  .selected {
    background: #00a5ff;
    color: white;
    font-weight: 600;
  }
}
